import React, { Component } from "react";
import "./registr.css";
import axios from "../../../server/node_modules/axios";

class Registr extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userMail: "",
            userPassword: "",
            userPassword2: ""
        };
        
    }
    onInputChange = e => {
        let value = e.target.value;
        let inputType = e.target.type;

        let isEmail = inputType == "email" ? true : false;
        let temp = { ...this.state };

        if (isEmail) {
            temp.userMail = value;
        } else {
            temp.userPassword = value;
        }
        this.setState(temp);
    };
    registerUser = () => {
        console.log(this.state);
    };

    render() {
        return (
            <div className="registr-user">
                <h1>Registr Page</h1>
                <input
                    type="email"
                    required="required"
                    placeholder="email"
                    onChange={this.onInputChange}
                />
                <input
                    type="password"
                    required="required"
                    placeholder="password"
                    onChange={this.onInputChange}
                />
                <input type="password" required="required" placeholder="password2" />
                <button onClick={this.registerUser()}>הירשם</button>
            </div>
        );
    }
}

export default Registr;
