import React, { Component } from 'react';
import { BrowserRouter, Link, Switch, Route } from 'react-router-dom';
import '../App.css';
import Login from './Login';
// import SearchArea from './search_Item_results';
import Home from './Home';
import MovieList from './movie_list_Results';
import Registr from './Registr';


class App extends Component {
  
  render() {

    return (
      <BrowserRouter>
        <div className="App">
                <div className="LinkHead">

                   <Link to='/'>Home</Link>
                  <Link to='/Login'>Login</Link>
                  <Link to='/Registr'>Registr</Link>
                  <Link to='/search_item_results'>Search</Link>

                 </div>

          <Switch>
            < Route exact path='/' component={Home} />
            < Route exact path='/Login' component={Login} />
            <Route exact path='/MovieList' component={MovieList}/>
            <Route exact path='/Registr' component={Registr}/>
            
           
          

            
            



          </Switch>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
